import React, { Component } from 'react';
import {Form, FormGroup, Input , Label, Button } from 'reactstrap'
import axios from 'axios'

class Contact extends Component {
    constructor(){
        super()
       this.state = {
            email: {
              recipient: 'msulman147@gmail.com',
              sender: 'waleedmuaz2@gmail.com',
              subject: 'abc',
              text: '12345'
            }
          }
      
    }
    sendEmail = () => {
        const { email } = this.state;
        fetch(`http://127.0.0.1:4567/send-email?recipient=${email.recipient}&sender=${email.sender}&topic=${email.subject}&text=${email.text}`) //query string url
          .catch(err => console.error(err))
      }

    render() {
        const { email } = this.state;
        const spacer = {
          margin: 10
        }
        const textArea = {
          borderRadius: 4
        }
        return (
          <div className="App">
            <div style={{ marginTop: 10 }} >
              <h2> Send Email </h2>
              <label> Recipient </label>
              <br />
              <input value={email.recipient}
                onChange={e => this.setState({ email: { email, recipient: e.target.value } })} />
              <div style={spacer} />
              <label> Sender </label>
              <br />
              <input value={email.sender}
                onChange={e => this.setState({ email: { email, sender: e.target.value } })} />
              <div style={spacer} />
              <label> Subject </label>
              <br />
              <input value={email.subject}
                onChange={e => this.setState({ email: { email, subject: e.target.value } })} />
              <div style={spacer} />
              <label> Message </label>
              <br />
              <textarea rows={3} value={email.text} style={textArea}
                onChange={e => this.setState({ email: { email, text: e.target.value } })} />
              <div style={spacer} />
              <button onClick={this.sendEmail}> Send Email </button>
            </div>
          </div>
        );
      }
    }
    export default Contact;
